import { useEffect, useRef } from "react";
import { useSelector } from "react-redux";

import { useAppDispatch } from "shared/store";
import { TModalRef } from "shared/types";
import { Button } from "shared/ui/button";
import { Modal } from "shared/ui/modal";
import { selectLocalData, selectRemoteData } from "features/sync/slice/actions";
import { selectCurrentVersion, selectHasConflicts, selectRemoteVersion } from "features/sync/slice/selectors";

export const Conflicts = () => {
  const dispatch = useAppDispatch();
  const hasConflicts = useSelector(selectHasConflicts);
  const currentVersion = useSelector(selectCurrentVersion)
  const remoteVersion = useSelector(selectRemoteVersion)

  const modalRef = useRef<TModalRef | null>(null);

  const onCloseModal = () => {
    modalRef.current?.close();
    dispatch(selectLocalData());
  };

  const onSelectRemote = () => {
    modalRef.current?.close();
    dispatch(selectRemoteData());
  };

  useEffect(() => {
    if (hasConflicts) {
      modalRef.current?.open();
    }
  }, [hasConflicts]);

  return (
    <Modal ref={modalRef} title="Конфликт данных" onClose={onCloseModal}>
      <p>Данные на устройстве отличаются от данных, сохраненных на сервере.</p>
      <p>{currentVersion > remoteVersion ? 'Данные на устройстве более свежие. Возможно, последняя синхронизация прошла неудачно.' : 'Данные на сервере более свежие. Возможно, вы использовали приложение с другого устройства.'}</p>
      <Button onClick={onCloseModal}>Оставить локальные данные {currentVersion > remoteVersion && <span>(более свежие)</span>}</Button>
      <Button onClick={onSelectRemote}>Обновить с сервера {currentVersion > remoteVersion && <span>(более свежие)</span>}</Button>
    </Modal>
  );
};
